import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider
  // InputNumber,
  // TreeSelect
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/costToSalesRatioByItemReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";

const loop = array =>
  array.map((data, key) => {
    return {
      title: `${data.item_category_code} (${data.item_category_name})`,
      value: data.id_item_category,
      key: key,
      children: data.children ? loop(data.children) : null
      // disabled: data.children.length > 0 ? true : false
    };
  });

class CostToSalesRatioByItemReport extends Component {
  state = {
    filter: {
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null,
      sort_by: "item_code",
      sort_type: "asc",
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss")
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getCostToSalesRatioByItemReports } = this.props;
    getCostToSalesRatioByItemReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.costToSalesRatioByItemReport.error) {
      notification("error", this.props.costToSalesRatioByItemReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.costToSalesRatioByItemReport.success) {
      notification("success", this.props.costToSalesRatioByItemReport.success);
      this.props.clearSuccess();
    }
  }

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getCostToSalesRatioByItemReports } = this.props;
    //const { current_page } = this.props.costToSalesRatioByItemReport;
    let filter = this.filterValue();

    getCostToSalesRatioByItemReports(filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getCostToSalesRatioByItemReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        sort_by: "item_code",
        sort_type: "asc",
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss")
      }
    });
    getCostToSalesRatioByItemReports();
  };

  render() {
    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        rowKey: "item_code",
        width: "7%"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        rowKey: "item_name"
      },
      {
        title: getLanguage({ id: "totalQuantity" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        width: "8%",
        align: "right",
        parse: true,
        render: text => {
          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "salesAmount" }),
        dataIndex: "sales_amount",
        rowKey: "sales_amount",
        width: "15%",
        align: "right",
        parse: true,
        render: text => {
          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "cogs" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "amount" }),
            dataIndex: "cogs_amount",
            rowKey: "cogs_amount",
            width: "15%",
            align: "right",
            parse: true,
            spanCheck: "string",
            spanCheckValue: "Total",
            spanValue: 1,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          },
          {
            title: getLanguage({ id: "ratio" }),
            dataIndex: "cogs_ratio_percent",
            rowKey: "cogs_ratio_percent",
            width: "15%",
            align: "right",
            // parse: true,
            spanCheck: "string",
            spanCheckValue: "Total",
            spanValue: 1,
            render: (text, row) => {
              if (row.cogs_ratio || row.cogs_ratio === 0) {
                return (
                  <NumberFormat value={parseFloat(row.cogs_ratio)} suffix="%" />
                );
              } else return "0%";
            }
          }
        ]
      },
      {
        title: getLanguage({ id: "grossProfit" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "amount" }),
            dataIndex: "gross_profit_amount",
            rowKey: "gross_profit_amount",
            width: "15%",
            align: "right",
            parse: true,
            spanCheck: "string",
            spanCheckValue: "Total",
            spanValue: 1,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          },
          {
            title: getLanguage({ id: "ratio" }),
            dataIndex: "gross_profit_ratio_percent",
            rowKey: "gross_profit_ratio_percent",
            width: "15%",
            align: "right",
            // parse: true,
            spanCheck: "string",
            spanCheckValue: "Total",
            spanValue: 1,
            render: (text, row) => {
              if (row.gross_profit_ratio || row.gross_profit_ratio === 0)
                return (
                  <NumberFormat
                    value={parseFloat(row.gross_profit_ratio)}
                    suffix="%"
                  ></NumberFormat>
                );
              else return "0%";
            }
          }
        ]
      }
    ];

    // Data setup
    const {
      cost_to_sales_ratio_by_item_reports,
      locations,
      filter,
      filter_select
    } = this.props.costToSalesRatioByItemReport;
    //console.log(this.props.costToSalesRatioByItemReport.base_currency); //console.log(this.state);
    const dataSource = [];
    const excelDataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    cost_to_sales_ratio_by_item_reports.map((data, key) => {
      const modifier = { key: key + 1 };

      var cogs_ratio_percent = parseFloat(data.cogs_ratio ? data.cogs_ratio : 0)
        .toFixed(2)
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      cogs_ratio_percent = cogs_ratio_percent + "%";

      var gross_profit_ratio_percent = parseFloat(
        data.gross_profit_ratio ? data.gross_profit_ratio : 0
      )
        .toFixed(2)
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      gross_profit_ratio_percent = gross_profit_ratio_percent + "%";

      excelDataSource.push({
        ...data,
        ...modifier,
        cogs_ratio_percent: cogs_ratio_percent,
        gross_profit_ratio_percent: gross_profit_ratio_percent
      });

      return dataSource.push({
        ...data,
        ...modifier
      });
    });

    const { loading, loadingForeign } = this.props.costToSalesRatioByItemReport;

    const fileName = getLanguage({ id: "costToSalesRatioByItemReports.pageHeader" });
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={
              <IntlMessages id="costToSalesRatioByItemReports.pageHeader" />
            }
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={
                <IntlMessages id="costToSalesRatioByItemReports.pageHeader" />
              }
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              scale={0.6}
              title="costToSalesRatioByItemReports.pageHeader"
            />

            <ExcelWrapper
              title="costToSalesRatioByItemReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={excelDataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "100%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>
                <Row justify="space-between" type="flex">
                  <Col span={12}>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="stockValuationReports.sortBy" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(this, "sort_by")}
                        value={
                          this.state.filter.sort_by
                            ? this.state.filter.sort_by
                            : "item_code"
                        }
                        style={{ width: "95%" }}
                        // placeholder={
                        //   <IntlMessages id="stockValuationReports.sortBy.select" />
                        // }
                        optionFilterProp="children"
                        filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value="item_code">
                          <IntlMessages id="itemCode" />
                        </Option>
                        <Option key={2} value="total_qty">
                          <IntlMessages id="salesOrders.totalQty" />
                        </Option>
                        <Option key={3} value="sales_amount">
                          <IntlMessages id="costToSalesRatioReports.filter.netSalesAmount" />
                        </Option>
                        <Option key={4} value="cogs_amount">
                          <IntlMessages id="costToSalesRatioReports.filter.cogsAmount" />
                        </Option>
                      </Select>
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="sortType" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "sort_type"
                        )}
                        value={
                          this.state.filter.sort_type
                            ? this.state.filter.sort_type
                            : "asc"
                        }
                        style={{ width: "90%" }}
                        // placeholder={
                        //   <IntlMessages id="stockValuationReports.sortBy.select" />
                        // }
                        optionFilterProp="children"
                        filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value="asc">
                          <IntlMessages id="ascending" />
                        </Option>
                        <Option key={2} value="desc">
                          <IntlMessages id="descending" />
                        </Option>
                      </Select>
                    </Col>
                  </Col>
                  <Col span={12} style={{ display: "flex" }}>
                    <Row
                      justify="start"
                      gutter={1}
                      type="flex"
                      style={{ width: "100%", marginTop: "auto" }}
                    >
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : cost_to_sales_ratio_by_item_reports.length === 0 &&
              !this.props.costToSalesRatioByItemReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="costToSalesRatioByItemReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    costToSalesRatioByItemReport: state.costToSalesRatioByItemReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  CostToSalesRatioByItemReport
);
