const actions = {
  getCostToSalesRatioByItemReports: (filter, filter_select) => ({
    type: "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_REQUEST",
    payload: {
      filter: filter,
      filter_select: filter_select
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
}

export default actions